  font-family: 'Gotham';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}

/* CSS Reset */

html:before {
  display: none
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
code,
del,
dfn,
em,
img,
q,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
textarea,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: inherit;
  font-style: inherit;
  font-size: 100%;
  font-family: 'Gotham';
  vertical-align: baseline;
}
html,
body {
  height: 100%;
  min-height: 100%;
  width: 100%;
  font-size: 16px;
}
a, a:link, a:visited, a:focus, a:hover, a:active {
  text-decoration: none;
}
a,
span {
  line-height: inherit
}
ul {
  list-style: none
}
body {
  background-color: #fff;
  color: #000033;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
table {
  border-collapse: separate;
  border-spacing: 0
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: ""
}
blockquote,
q {
  quotes: """"
}

.yellowLine {
    border-top: 8px solid yellow;
    padding-top: 0.5em;
}

.blueLine {
    border-top: 8px solid blue;
    padding-top: 0.5em;
}

.space-around {
  padding-top: 2em;
  padding-bottom: 2em;
}

.canvas-container {
  line-height: 0;
}



